*html {
  position: relative;
}
* h1,
p,
h2,
h3 {
  color: white;
  font-family: sans-serif;
}

* p a {
  color: #eef6ebda;
  font-family: sans-serif;
}

@media all and (min-width:0px) and (max-width: 320px) {
}
.TitleImage {
  height: 20vw;
  width: 20vw;
}

.Intro p{
    color:#141414;
    font-size: 2vmax;
}

.Intro h1{
    color:#141414;
    font-size: 4vmax;
}
.Intro {
  top:0;
  position:relative;  
  display: block;
  height: 105vh;
  width: 100vw;
  background: #f1f1f1;
  color: black;
}

.IntroDiv {
  padding-top: 35vh;
